import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import { useFetcher } from 'shared/components/ajax';
import Navbar from "shared/components/ui/Navbar";
import { Button, List, ListItem, withStyles, ListItemText, ListItemSecondaryAction, ListItemIcon } from "@material-ui/core";
import { navigate } from "gatsby";
import { expandImg } from "shared/components/util";
import Edit from "@material-ui/icons/Edit";
import Goods from '@material-ui/icons/ShoppingCart';
import Add from "@material-ui/icons/Add";
/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    head: {
        display: 'flex',
        alignItems: 'center',
    },
    button: {
        margin: 20,
        width: 'calc(100% - 40px)',
        '& .MuiButton-label': {
            flexDirection: 'column',
            display: 'flex',
        },
        '& .MuiSvgIcon-root': {
            marginBottom: theme.spacing(1),
            objectFit: 'contain',
            width: '40px',
            height: '40px',
        }
    },
});

const Page = withStyles(styles)(({ classes }) => {
    const user = useUserInfo();
    const store = useFetcher(user && user.features.store && '/market/store/info');
    const storeGoods = useFetcher(store && '/market/store/goods');
    const storeOrders = useFetcher(store && '/market/store/orders');
    return (
        <>
            <Navbar />
            <Defer data={user}>
                {() => (
                    <List>
                        {user.features.store && <Defer data={store}>
                            {() => (<>
                                <ListItem>
                                    <ListItemIcon>
                                        <img src={expandImg(store.data.logo?.id)} alt="" width="100" height="100" />
                                    </ListItemIcon>
                                    <ListItemText primary={store.data.title} />
                                    <ListItemSecondaryAction>
                                        <Button startIcon={<Edit />} onClick={() => navigate('/store/edit/')} color="primary" variant="contained">
                                            Edit Profil
                                        </Button>
                                    </ListItemSecondaryAction>
                                </ListItem>
                            </>)}
                        </Defer>}
                        {user.features.store && <Defer data={storeGoods}>
                            {() => (<>
                                <ListItem>
                                    <ListItemIcon>
                                        <Goods />
                                    </ListItemIcon>
                                    <ListItemText primary={"Daftar Barang"} />
                                    <ListItemSecondaryAction>
                                        <Button startIcon={<Add />} onClick={() => navigate('/store/goods/post/')} color="primary" variant="contained">
                                            Tambah Barang
                                        </Button>
                                    </ListItemSecondaryAction>
                                </ListItem>
                            </>)}
                        </Defer>}
                        {!user.features.store && <ListItem>
                            <ListItemText primary="Anda belum mendaftar sebagai agen toko" />
                            <ListItemSecondaryAction>
                                <Button variant="contained" color="primary"
                                    onClick={() => navigate('/store/register')}>Daftar disini</Button>
                            </ListItemSecondaryAction>
                        </ListItem>}
                    </List>
                )}
            </Defer>
        </>
    );
});

export default Page;
